import React from 'react';
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, Brush, ReferenceLine
} from 'recharts';

class UDLineChart extends React.Component {

  render() {
    return (
      <LineChart
        layout={this.props.layout}
        width={this.props.width}
        height={this.props.height}
        data={this.props.data}
      >
        <CartesianGrid stroke={this.props.gridStroke} strokeDasharray={this.props.strokeDasharray} />
        <XAxis dataKey={this.props.dataKeyX} type={this.props.typeX} orientation={this.props.orientationX} interval={this.props.intervalX} dx={this.props.dx} />
        <YAxis dataKey={this.props.dataKeyY} type={this.props.typeY} />
        <Tooltip />
        <Legend verticalAlign={this.props.legendAlign} height={this.props.legendHeight} />
        <ReferenceLine x={this.props.RefLineX} stroke={this.props.RefStrokeX} label={this.props.RefLabelX} />
        <ReferenceLine y={this.props.RefLineY} stroke={this.props.RefStrokeY} label={this.props.RefLabelY} />
        <Line connectNulls type={this.props.type1}
          dataKey={this.props.dataKey1}
          stroke={this.props.stroke1}
          activeDot={{ r: this.props.activeDot }}
          animationBegin={this.props.animationBegin1}
          animationDuration={this.props.animationDuration1}
          animationEasing={this.props.animationEasing1}
          legendType={this.props.legendType1}
        />
        <Line connectNulls type={this.props.type2}
          dataKey={this.props.dataKey2}
          stroke={this.props.stroke2}
          activeDot={{ r: this.props.activeDot }}
          animationBegin={this.props.animationBegin2}
          animationDuration={this.props.animationDuration2}
          animationEasing={this.props.animationEasing2}
          legendType={this.props.legendType2}
        />
        <Brush />
      </LineChart>
    )

  }
}

export default UDLineChart
